import React from 'react';
import { hydrate } from 'react-dom';
import SearchBox from './patterns/search-box/searchBox';
import { finderConfig } from './patterns/finder/funnelback';

document.addEventListener('DOMContentLoaded', () => {
    if (/(Trident|MSIE)/.test(navigator.userAgent)) {
        return;
    } else {
        const searchBox = document.querySelector('#search-box');
        searchBox &&
            finderConfig(searchBox.dataset.config).then((config) => {
                hydrate(
                    <SearchBox element={searchBox} config={config} />,
                    searchBox
                );
            });
    }
});
