import React from 'react';
import PropTypes from 'prop-types';

function Select(props) {
    const currentValue = props.query.facets[props.facet.meta] || '';

    const setFacet = (value) => {
        const newQuery = props.query;

        if (value) {
            newQuery.facets[props.facet.meta] = value;
        } else {
            delete newQuery.facets[props.facet.meta];
        }

        newQuery.startRank = 1;
        newQuery.misspelling = null;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    return (
        <div
            className={`search-box__filter search-box__select ${
                currentValue && 'search-box__select--selected'
            }`}
        >
            <label
                className="sr-only"
                htmlFor={`meta_${props.facet.meta}_sand--`}
            >
                {props.facet.name}
            </label>
            <select
                name={`meta_${props.facet.meta}_sand`}
                id={`meta_${props.facet.meta}_sand--`}
                onChange={(e) => setFacet(e.target.value)}
                value={currentValue}
            >
                <option
                    value=""
                    id={`meta${props.facet.meta}all`}
                    name={`meta_${props.facet.meta}_sand--`}
                >
                    {props.facet.noSelection}
                </option>

                {props.facet.values.map((value, i) => {
                    return (
                        <option key={i} value={value.data.toLowerCase()}>
                            {value.label}
                        </option>
                    );
                })}
            </select>
            <span className="fas fa-sharp fa-chevron-down icon"></span>
        </div>
    );
}

Select.propTypes = {
    facet: PropTypes.object,
    query: PropTypes.object,
    update: PropTypes.object,
};

export default Select;
