import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { suggest } from '../../finder/funnelback';
import Clear from '../../finder/components/query/finder__clear';

const maximumSuggestions = 5,
    escapeKey = 'Escape',
    endKey = 'End',
    homeKey = 'Home',
    arrowUp = 'ArrowUp',
    arrowDown = 'ArrowDown';

function highlightQueryTerm(suggestion, partialQuery) {
    return (
        <React.Fragment>
            <span>
                <span className="sr-only">{suggestion}</span>
                <span aria-hidden="true">
                    <span className="search-box__suggestion">
                        {suggestion.slice(
                            suggestion.indexOf(partialQuery.toLowerCase()),
                            partialQuery.length
                        )}
                    </span>
                    {suggestion.slice(
                        suggestion.indexOf(partialQuery.toLowerCase()) +
                            partialQuery.length
                    )}
                </span>
            </span>
        </React.Fragment>
    );
}

/**
 * Search input field and autocomplete.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Query(props) {
    // save what they're typing
    const [partialQuery, setPartialQuery] = useState('');
    // Funnelback suggestions for the currently typed text
    const [suggestions, setSuggestions] = useState([]);
    // Request token for calls to the Funnelback suggestions service, so we
    // can cancel it
    const [call, setCall] = useState({ cancel: () => {} });
    // ref for the input field, so we can .focus() it
    const [inputId] = useState('search-box--' + props.query.collection);

    const [showSuggestions, setShowSuggestions] = useState(false),
        [activeSuggestionID, setActiveSuggestionID] = useState('');

    useEffect(() => {
        setPartialQuery(props.query.query);
    }, [props.query.query, props.updating]);

    let textInput = null;
    function focusInput() {
        textInput.focus();
    }

    const submitForm = (query) => {
        call.cancel();

        const newQuery = props.query;

        newQuery.misspelling = null;
        newQuery.query = query ? query : partialQuery ? partialQuery : '';
        newQuery.sortType = partialQuery ? '' : props.config.sort[0].type;
        newQuery.startRank = 1;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    // on clear, make a default request for results
    const clearQuery = () => {
        call.cancel();
        setPartialQuery('');
        setSuggestions([]);
        focusInput();

        // const newQuery = props.query;

        // newQuery.misspelling = null;
        // newQuery.query = '';
        // newQuery.sortType = props.config.sort[0].type;
        // newQuery.startRank = 1;
        // newQuery.interacted = true;

        // props.update.query(newQuery);
        // props.update.results(!props.update.updateState);
    };

    // const submitForm = (query) => {
    //     call.cancel();
    //     setSuggestions([]);

    //     const newQuery = props.query;

    //     newQuery.misspelling = null;
    //     newQuery.query = query ? query : partialQuery ? partialQuery : '';
    //     newQuery.sortType = partialQuery ? '' : props.config.sort[0].type;
    //     newQuery.startRank = 1;
    //     newQuery.interacted = true;

    //     props.update.query(newQuery);
    //     props.update.results(!props.update.updateState);
    // };

    const clear = partialQuery && (
        <Clear
            clear={() => {
                clearQuery();
            }}
        />
    );

    const submitSuggestion = (suggestion) => {
        setShowSuggestions(false);
        setSuggestions([]);
        focusInput();
        submitForm(suggestion);
    };

    // render suggestions
    const suggestionsList = suggestions && suggestions.length > 0 && (
        <ul
            role="listbox"
            aria-label="Search suggestions"
            aria-activedescendant={activeSuggestionID}
            className={
                showSuggestions
                    ? 'search-box__query__suggestions show'
                    : 'search-box__query__suggestions hide'
            }
        >
            {[...new Set(suggestions)]
                .slice(0, maximumSuggestions)
                .map((suggestion, i) => (
                    <li key={suggestion} role="option" id={inputId + '--' + i}>
                        <button
                            type="button"
                            onBlur={() => {
                                setActiveSuggestionID('');
                                setShowSuggestions(false);
                            }}
                            onFocus={() => setShowSuggestions(true)}
                            onMouseDown={() => submitSuggestion(suggestion)}
                            onClick={() => submitSuggestion(suggestion)}
                            onKeyDown={(e) => {
                                switch (e.key) {
                                    case escapeKey:
                                        e.target.parentNode.parentNode.parentNode
                                            .querySelector('input')
                                            .focus();
                                        setSuggestions([]);
                                        setActiveSuggestionID('');
                                        break;
                                    case arrowUp:
                                        if (
                                            e.target.parentNode
                                                .previousElementSibling &&
                                            e.target.parentNode.previousElementSibling.querySelector(
                                                'button'
                                            )
                                        ) {
                                            e.preventDefault();
                                            e.target.parentNode.previousElementSibling
                                                .querySelector('button')
                                                .focus();
                                            setActiveSuggestionID(
                                                e.target.parentNode
                                                    .previousElementSibling.id
                                            );
                                        } else {
                                            e.preventDefault();
                                            e.target.parentNode.parentNode.parentNode
                                                .querySelector('input')
                                                .focus();
                                            setActiveSuggestionID('');
                                        }
                                        break;
                                    case arrowDown:
                                        e.preventDefault();
                                        if (
                                            e.target.parentNode
                                                .nextElementSibling &&
                                            e.target.parentNode.nextElementSibling.querySelector(
                                                'button'
                                            )
                                        ) {
                                            e.preventDefault();
                                            e.target.parentNode.nextElementSibling
                                                .querySelector('button')
                                                .focus();
                                            setActiveSuggestionID(
                                                e.target.parentNode
                                                    .nextElementSibling.id
                                            );
                                        }
                                        break;
                                    case homeKey:
                                        e.preventDefault();
                                        if (
                                            e.target.parentNode.parentNode
                                                .firstChild &&
                                            e.target.parentNode.parentNode.firstChild.querySelector(
                                                'button'
                                            )
                                        ) {
                                            e.preventDefault();
                                            e.target.parentNode.parentNode.firstChild
                                                .querySelector('button')
                                                .focus();
                                            setActiveSuggestionID(
                                                e.target.parentNode.firstChild
                                                    .id
                                            );
                                        }
                                        break;
                                    case endKey:
                                        e.preventDefault();
                                        if (
                                            e.target.parentNode.parentNode
                                                .lastChild &&
                                            e.target.parentNode.parentNode.lastChild.querySelector(
                                                'button'
                                            )
                                        ) {
                                            e.preventDefault();
                                            e.target.parentNode.parentNode.lastChild
                                                .querySelector('button')
                                                .focus();
                                            setActiveSuggestionID(
                                                e.target.parentNode.lastChild.id
                                            );
                                        }
                                        break;
                                    default:
                                        break;
                                }
                            }}
                        >
                            {highlightQueryTerm(suggestion, partialQuery)}
                        </button>
                    </li>
                ))}
        </ul>
    );

    const input = (
        <div className="search-box__query__input">
            <label className="sr-only" htmlFor={inputId}>
                {'Search'}
            </label>
            <input
                autoComplete="off"
                id={inputId}
                name="query"
                placeholder="Enter course title or keyword"
                ref={(input) => {
                    textInput = input;
                }}
                type="text"
                value={partialQuery}
                onKeyDown={(e) => {
                    switch (e.key) {
                        case escapeKey:
                            clearQuery();
                            break;
                        case arrowDown:
                            if (suggestions && suggestions.length > 0) {
                                e.preventDefault();
                                e.target.parentNode
                                    .querySelector(
                                        '.search-box__query__suggestions button'
                                    )
                                    .focus();
                                setActiveSuggestionID(
                                    e.target.parentNode.querySelector(
                                        '.search-box__query__suggestions li'
                                    ).id
                                );
                            }
                            break;
                        default:
                            break;
                    }
                }}
                onFocus={() => setShowSuggestions(true)}
                onBlur={() => setShowSuggestions(false)}
                onChange={(e) => {
                    //clear old suggestions
                    setSuggestions([]);
                    // keep  what they're typing
                    setPartialQuery(e.target.value);

                    /**
                     * if we have a request to the suggestions service in progress,
                     * cancel it. Stops old suggestions overwriting new ones because
                     * the requests can't keep up with fast typing.
                     */
                    call.cancel();

                    if (e.target.value) {
                        // input is populated, ask for suggestions
                        const [suggestionsPromise, newCall] = suggest(
                            'city-university~sp-web2020-courses',
                            e.target.value
                        );

                        // update our request cancel function for the new request
                        setCall({
                            cancel: () => {
                                newCall.cancel();
                            },
                        });

                        suggestionsPromise
                            .then((data) => setSuggestions(data))
                            .catch(() => setSuggestions([]));
                    } else {
                        // input is empty, empty suggestions
                        setSuggestions([]);
                    }
                }}
            />
            {suggestionsList}
            {clear}
        </div>
    );

    return (
        <fieldset className="search-box__query">
            <div>
                {input}
                <button type="submit" className="search-box__query__submit">
                    <span
                        className="fal fa-sharp fa-search search-box__query__submit__icon"
                        aria-hidden="true"
                    />{' '}
                    <span className="sr-only search-box__query__submit__text">
                        Find
                    </span>
                </button>
            </div>
        </fieldset>
    );
}

Query.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    update: PropTypes.object,
    updating: PropTypes.bool,
};

export default Query;
